/* ft */
.ft {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
}
.ft__action {
	border: none;
	margin: 0 5px;
	display: inline-block;
	padding: 8px 15px;
	cursor: pointer;
	border-radius: 2px;
	-webkit-transition: all 0.3s;
	-o-transition: all 0.3s;
	transition: all 0.3s;
}
.ft__action span {
  position: relative;
}
.ft__action_prev {
	background-color: #535151;
	color: #fff;
}
.ft__action_prev:hover {
  background-color: #3b3b3b;
}
.ft__action_prev span {
  padding-left: 15px;
}
.ft__action_prev span:before {
  content: '<';
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}
.ft__action_next {
	background-color: #018695;
	color: #fff;
}
.ft__action_next:disabled {
	cursor: no-drop;
  opacity: 0.5;
}
.ft__action_next:disabled:hover {
	background-color: #018695;
}
.ft__action_next:hover {
  background-color: #01717e;
}
.ft__action_next span {
  padding-right: 15px;
}
.ft__action_next span:before {
  content: '>';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}
.ft__action_finish {
	background-color: #018695;
	color: #fff;
}
.ft__action_finish:hover {
  background-color: #01717e;
}

/* md */
.md {
  opacity: 0;
  visibility: hidden;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1000;
  background-color: rgba(0,0,0,0.8);
  overflow-y: auto;
  padding: 30px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
}
.md.active {
  opacity: 1;
  visibility: visible;
}
.md__close {
  position: absolute;
  top: 0;
  right: 0;
  width: 40px;
  height: 40px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  text-indent: -999999px;
  cursor: pointer;
}
.md__close:before,
.md__close:after {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  width: 20px;
  height: 2px;
  background-color: #018695;
}
.md__close:before {
  -webkit-transform: rotate(-45deg);
      -ms-transform: rotate(-45deg);
          transform: rotate(-45deg);
}
.md__close:after {
  -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
          transform: rotate(45deg);
}
.md__inner {
  background-color: #000;
  padding: 30px;
  position: relative;
  width: 400px;
}
.md__ttl {
  font-size: 20px;
  text-align: center;
  margin: 0 0 15px;
}

